import React, { useContext } from 'react';
import { authContext } from '../context/AuthContext'
import Login from './Login';
import Home from './Home';
const Root = () => {
    const { auth } = useContext(authContext);
    return (
        <div>
            {auth.token !== '' ? <Home /> : <Login />}
        </div>
    );
}

export default Root;